#h-categories {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 2em;
    gap: 2em;
}

#h-categories > *:hover {
    cursor: pointer;
}

#h-categories>*:after {
    content: "";
    display: block;
    background: var(--accent);
    width: 0%;
    height: .12em;
    position:relative;
    left: 50%;
    transition: width .3s ease, left .3s ease;
}

#h-categories>*:hover:after {
    width: 100%;
    left: 0;
}

#h-categories>*.selected:after {
    width: 100%;
    left: 0;
}

